<template>
  <div class='relative'>
    <input :type='passwordVisible ? "text" : "password"' 
     class="input-textfield-line"
     :class='{ "input-textfield-invalid": v$.modelValue.$error }' 
     placeholder='Ваш пароль'
     maxlength="32"
     :value="modelValue"
     @input="$emit('update:modelValue', $event.target.value.trim())"
    >
    <span class="material-icons-outlined centered" style="left:92%" @click='passwordVisible = !passwordVisible'>
      {{!passwordVisible ? "visibility_off" : "visibility"}}
    </span>
  </div>
  <div v-if='v$.modelValue.$error'>
    <div class='input-error-message' v-if='v$.modelValue.required.$invalid'>Поле має бути заповнене</div>
    <div class='input-error-message' v-else-if='v$.modelValue.minLength.$invalid'>Пароль має бути довшим за {{ min }} символів</div>
    <div class='input-error-message' v-else-if='v$.modelValue.format.$invalid'>Пароль може містити лише латинські літери, цифри і символи !#$%^*</div>
  </div>
</template>

<script>
const format = function(str) { return /^[A-Za-z0-9!#$%^*]+$/.test(str) }
import useVuelidate from '@vuelidate/core'
import { required, minLength, maxLength } from '@vuelidate/validators'
export default {
  props: {
    modelValue: { type: String, required: false, default: '' },
    min: { type: [String, Number], required: false, default: 6 },
  },
  data() { return {
    v$: useVuelidate(),
    passwordVisible: false,
  }},
  validations() { return {
    modelValue: { 
      required,
      minLength: minLength(this.min),
      format 
    },
  }},
}
</script>