<template>
  <auth-layout>

    <div class="auth-form">
      <form @submit.prevent='register()'>
        <div class="material-icons-outlined auth-icon">account_circle</div>
        <h2>РЕЄСТРАЦІЯ</h2>
        <input-text v-model='username' placeholder="Ваш логін" :isAlphanumeric='true' ref='username'/>
        <input-email v-model='email' ref='email'/>
        <input-password v-model='password' ref='password'/>
        <label>
          <input type="checkbox" id="privacy" v-model='privacyChecked'>
          <small>Ознайомлений з <router-link to='privacy'>умовами конфіденційності</router-link></small>
        </label>
        <button type='submit' class='button-plain auth-button' :disabled='!privacyChecked'>Створити акаунт</button>
        <small>Вже маєте акаунт? <router-link to='auth'>Увійти!</router-link></small>
      </form>
    </div>

  </auth-layout>
</template>

<script>
import authLayout from '@/layouts/auth-layout.vue'
import inputText from '@/components/inputs/input-text.vue'
import inputEmail from '@/components/inputs/input-email.vue'
import inputPassword from '@/components/inputs/input-password.vue'
export default {
  components: { authLayout, inputText, inputEmail, inputPassword },
  data() { return {
    privacyChecked: false,
    username: '',
    email: '',
    password: '',
  }},
  computed: {
    inputs() { return Object.keys(this.$refs).map(e => this.$refs[e]) }
  },
  methods: {
    async register() { 
      this.inputs.map(e => e.v$.$touch())
      if (!this.inputs.some(e => e.v$.$error)) {
        await this.$store.dispatch('reg', {
          username: this.username,
          email: this.email,
          password: this.password
        })
        this.$gtag.event('reg', {
          'event_category': 'conversion',
          'event_label': 'New registration',
          'value': 1
        })
      }
    },
  },
}
</script>

<style lang="scss" scoped>
a {
  vertical-align: middle;
}
input[type='checkbox'] {
  margin: 15px 7px;
  margin-bottom: 15px;
  width: 15px;
  height: 15px;
  vertical-align: middle;
}
</style>